import { formatTime } from '../../../util/util.js'
import app from '../../../App.vue'
export default {
  props: {
    messageList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      TEMPLATE_ID: ['58HkYaZbPdos9agAkBJjpSy8II2elVNTTmG2Mki5o58'],
      formatTime
    }
  },
  computed: {
  	loginStatus() {
  		return this.$store.state.loginStatus
  	},
    userInfo() {
      return this.$store.state.userInfo
    }
  },
  methods: {
    handleGoChat(item) {
      const self = this

      console.log(item)
      console.log(this.userInfo)

      if (this.userInfo.isService === 1) {
        console.log('本人是公司客服')
        self.$openPage({
          name: 'chat',
          query: {
            targetId: item.targetId + '',
            avatarUrl: item.latestMessage.content.user.id == self.userInfo.userId ? item.latestMessage.content.user.meportrait : (item.latestMessage.content.user.meid == self.userInfo.userId ? item.latestMessage.content.user.portrait : ''),
            userName: item.latestMessage.content.user.id == self.userInfo.userId ? item.latestMessage.content.user.mename : (item.latestMessage.content.user.meid == self.userInfo.userId ? item.latestMessage.content.user.name : '')
          } // 本人为接受者 ---》 取发送者的名字和头像       本人为发送者 ---》 取接受者的名字和头像
        })
      } else {
        console.log('本人不是公司客服')
        if (this.userInfo.isSubscribe === 1) {
          console.log('已订阅')
          self.$openPage({
            name: 'chat',
            query: {
              targetId: item.targetId + '',
              avatarUrl: item.latestMessage.content.user.id == self.userInfo.userId ? item.latestMessage.content.user.meportrait : (item.latestMessage.content.user.meid == self.userInfo.userId ? item.latestMessage.content.user.portrait : ''),
              userName: item.latestMessage.content.user.id == self.userInfo.userId ? item.latestMessage.content.user.mename : (item.latestMessage.content.user.meid == self.userInfo.userId ? item.latestMessage.content.user.name : '')
            }
          })
        } else {
          console.log('未订阅')
          wx.requestSubscribeMessage({
            tmplIds: self.TEMPLATE_ID,
            success(res) {
              // 授权失败
              if (res[self.TEMPLATE_ID[0]] === 'reject') {

              }
              // 授权成功
              if (res[self.TEMPLATE_ID[0]] === 'accept') {
                self.chatSubscribe()
              }
            },
            complete(res) {
              self.$openPage({
                name: 'chat',
                query: {
                  targetId: item.targetId + '',
                  avatarUrl: item.latestMessage.content.user.id == self.userInfo.userId ? item.latestMessage.content.user.meportrait : (item.latestMessage.content.user.meid == self.userInfo.userId ? item.latestMessage.content.user.portrait : ''),
                  userName: item.latestMessage.content.user.id == self.userInfo.userId ? item.latestMessage.content.user.mename : (item.latestMessage.content.user.meid == self.userInfo.userId ? item.latestMessage.content.user.name : '')
                }
              })
            }
          })
        }
      }
    },
    // 后端埋点 告知后端推送订阅消息
    chatSubscribe() {
      const self = this
      self.$http
        .weChatSubscribe({})
        .then(res => {
          self.userInfo.isSubscribe = 1
          self.$store.commit('setUserInfo', this.userInfo)
        })
        .catch(err => {
          console.log(err)
        })
    },
    longpress(item) {
    	const self = this
    	console.log(item)
    	uni.showModal({
    	    title: '提示',
    	    content: '你确定要删除对话吗？',
    	    success: function(res) {
    	        if (res.confirm) {
    						app.globalData.Services.Conversation.remove(function(info) {
    							console.log(info)
              self.$emit('longpress')
    						}, {
              targetId: item.targetId
            })
    	        } else if (res.cancel) {
    	          console.log('用户点击取消')
    	        }
    	    }
    	})
    }
  }
}
